html{
  font-size: 62.5%; /* 16px * 62.5%  = 10px = 1rem*/
  box-sizing: border-box;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

@font-face {
  font-family: 'geomanistregular';
  src: url('./Geomanist-Regular-Webfont/geomanist-regular-webfont.eot');
  src: url('./Geomanist-Regular-Webfont/geomanist-regular-webfont.eot?#iefix') format('embedded-opentype'),
       url('./Geomanist-Regular-Webfont/geomanist-regular-webfont.woff2') format('woff2'),
       url('./Geomanist-Regular-Webfont/geomanist-regular-webfont.woff') format('woff'),
       url('./Geomanist-Regular-Webfont/geomanist-regular-webfont.ttf') format('truetype'),
       url('./Geomanist-Regular-Webfont/geomanist-regular-webfont.svg#geomanistregular') format('svg');
  font-weight: normal;
  font-style: normal;

}


#root{
  height: 100%;
}

body{
  margin: 0;
  height: 100vh;
  font-size: 1.6rem;
  font-family: 'geomanistregular', Helvetica, Arial, sans-serif;
  background-color: #F9F8F6;
}

/* Layout */
.grid-container{
  display: grid;
  grid-template-areas: 
      'header'
      'main'
      'footer';
  grid-template-columns: 1fr;
  grid-template-rows: 5rem 1fr 5rem;
  height: 100%;
}

header{
  grid-area: header;
  background-color: #DFD9D0;
}

main{
  grid-area: main;
  padding: 1rem;
}

footer{
  grid-area: footer;
  background-color: #1E1E1D;
  color: #F9F8F6;
}

/* Common */
h1{
  font-size: 1.8rem;
  padding: 1rem 0;
}

h2{
  font-size: 1.6rem;
  padding: 1rem 0;
}

a{
  text-decoration: none;
}

a:hover{
  color: orange;
}

ul{
  padding: 0;
  margin: 0;
  list-style-type: none;
}

li{
  margin-top: 1rem;
}

input, select, textarea, button {
  padding: 1rem;
  border-radius: 0.5rem;
  border: 0.1rem solid #a4a4a4;
  cursor: pointer;
  font-size: 1.6rem;
}

button{
  background-color: #f8f8f8;
}

input:hover, select:hover, textarea:hover {
  border: 0.1rem solid #404040;
}

button:hover{
  border: 0.1rem solid #404040;
}

button.primary{
  background-color: #f0c040;
}

button.block{
  width: 100%;
}

button.small{
  font-size: 1.2rem;
}

/* Header */
header a{
  color: #F9F8F6;
  padding: 1rem;
}

a.brand{
  font-size: 3rem;
  font-weight: bold;
}

.badge {
  background-color: #B9012F;
  color: #fff;
  border-radius: 50%;
  padding: 0.3rem .9rem;
  font-size: 1.4rem;
  margin-left: 0.2rem;
}

.row{
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
}

.row.top{
  align-items: flex-start;
}

.col-1{
  flex: 1 1 25rem;
}

.col-2{
  flex: 2 1 50rem;
}

.min-30{
  min-width: 30rem;
}

.row.center{
  justify-content: center;
}

/* image */
img{
  border-radius: 0.5rem;
}

img.small{
  max-width: 5rem;
  width: 100%;
}

img.medium{
  max-width: 29rem;
  width: 100%;
}

img.large{
  width: 100%;
}



/* Card */
.card{
  border: 0.1rem #c0c0c0 solid;
  background-color:#F9F8F6;
  border-radius: 0.5rem;
  margin: 1rem;
}

.card-body{
  padding: 1rem;
}

.card-body > *{
  margin-bottom: 0.5rem;
}

.price{
  font-size: 2rem;
}

/* Rating */
.rating span{
  color: #D7BC8B;
  margin: 0.1rem;
}

.rating span:last-child{
  color: #1E1E1D;
}

/* Alert */
.loading{
  display: block !important;
}

.success{
  color: #20a020;
}

.danger{
  color: #a02020;
}

.alert{
  padding: 1rem;
  border: 0.1rem solid transparent;
  border-radius: 0.5rem;
}

.alert-info{
  color: #2020a0;
  background-color: #e0e0ff;
}

.alert-danger{
  color: #a02020;
  background-color: #ffe0e0;
}

.alert-success{
  color: #20a020;
  background-color: #eeffe0;
}

/* Form */
.form{
  max-width: 60rem;
  margin: 0 auto;
}
.form > div {
  display: flex;
  flex-direction: column;
  margin: 1rem;
}

.form label {
  margin: 1rem 0;
}

/* Dropdown */
.dropdown{
  display: inline-block;
  position: relative;
}
.dropdown-content{
  position: absolute;
  display: none;
  right: 0;
  min-width: 12rem;
  padding: 1rem;
  z-index: 1;
  background-color: #1E1E1D;
  margin: 0;
  margin-top: 0.4rem;
  border-radius: 0.5rem;
}

.dropdown:hover .dropdown-content{
  display: block;
}

/* Checkout Steps */
.checkoutsteps > div {
  border-top: 0.3rem solid #c0c0c0;
  color: #c0c0c0;
  flex: 1;
  padding: 1rem;
  font-weight: bold;
}

.checkoutsteps > div.active{
  border-top-color: #1E1E1D;
  color: #1E1E1D;
}

/* Table */
.table{
  width: 100%;
  border-collapse: collapse;
}

.table tbody tr:nth-of-type(odd){
  background-color: #f4f4f4;
}

.table td, .table th{
  text-align: left;
  border: 0.1rem solid #e4e4e4;
  padding: 0.5rem;
}

.table button{
  margin: 0 0.2rem;
}